
/* Centered flex-grid*/
.primary-nav,
.footer-grid {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    padding: 0.75rem;
    width: 100%;
}

/* Common to both pages*/
.main-content {
    min-height: 100%;
}

/* Common to both pages*/
.filter-grid:focus,
.filter-grid:active,
.reviews-grid:focus,
.reviews-grid:active {
    outline: none;
}